import React from 'react';
import logo from './logo.svg';
import './App.css';


//导入两个组件
import SearchList from './searchbox/SearchList.js';
import SearchBox from './searchbox/SearchBox.js';
import SearchHot from './searchbox/SearchHot.js';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state=({searchResults:[]})
    this.handleSearch = this.handleSearch.bind(this);
  }
  handleSearch(keyword){
    console.log("输入值:"+keyword);
    let result=['牛奶','饼干'];
    this.setState({searchResults:result});
  }
  render(){
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <SearchBox searchKeyword={this.handleSearch}/>
        <SearchHot/>
        <SearchList searchResults={this.state.searchResults}>
          <div>为您搜索到{this.state.searchResults.length }条记录</div>
        </SearchList>
      </div>
    );
  }
}

export default App;
